.select {
  padding: 8px 12px;
  border: 1px solid #d1d1d1;
  border-radius: 4px;
  font-size: 14px;
  outline: none;
  cursor: pointer;
  background-color: white;
  transition: border-color 0.2s ease;
}

.select:focus {
  border-color: #0066cc;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.2);
}

.option {
  padding: 8px;
}

.option:hover {
  background-color: #f5f5f5;
}